        <div class="art-base-info">
            <!-- <a href="#" style="font-size: 12px;">Online First, doi:10.32604/cmes.2020.011067 </a> -->
            <h3>
                Reliable Fuzzy Integral Sliding-Mode Control for Faulty Vehicle Active Suspension
            </h3>
            <span style="display: block;color: #4f5671;line-height: 25px;margin-bottom: 10px;font-size: 12px;">
                Wei Hu
            </span>
            <ul style="padding: 0;font-size: 12px;color: #4f5671;">
                <li>School of Automation, Huazhong University of Science and Technology, Wuhan 430074, China.</li>
                <!-- <li>2 Faculty of Engineering and Information Technology, University of Technology Sydney, NSW, 2007, Australia</li> -->
            </ul>
            <!-- <span style="font-size: 12px;color: #4f5671;">âˆ— Corresponding Author: Shiping Wen. </span> -->
        </div>

        <div class="download">
            <span><a href="https://stduestceducn-my.sharepoint.com/:b:/g/personal/201921080433_std_uestc_edu_cn/EUySC2OfyM9InkOIdySzRMgBJRecCZRn5UcvqGscrvzN-A?e=v6a4oa" rel="nofollow" class="pdf">Download PDF</a>  </span>
        </div>

        <div class="abstract">
            <h3>Abstract</h3>
            <p>
                A reliable fuzzy integral sliding-mode control is proposed for nonlinear active suspension systems with partial loss of actuator
effectiveness fault. The Takagi-Sugeno (T-S) fuzzy method is adopted on the background of the variation of the masses. The T-S
fuzzy system is utilized to depict the prescribed system to achieve the design aims via the sector nonlinearity approach. In the
existing results, the reachability of the proposed integral sliding mode is achieved and maintained without considering the system
perturbations. To shorten such gap, this paper proposes sufficient conditions to reach the sliding surface with considering the system
perturbations as well as asymptotic stability of the sliding motion by solving the given linear matrix inequalities. Finally, simulation
results are presented to demonstrate the effectiveness of the proposed schemes.
            </p>
        </div>

        <div class="keywords">
            <h3>Keywords</h3>
            <p>Reliable control, active suspension system, integral sliding-mode control, T-S fuzzy model</p>
        </div>


